.container-login{
}

.login-wrapper {
    width: 96%;
    max-width: 1240px;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    height: 100vh;
}

.header-login{
    width: 100%;
    padding: 24px 0;
}

.form-login {
    flex: 1 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.form-login form {
    height: fit-content;
    width: 400px;
    max-width: 96%;
    padding: 24px;
    border: 1px solid #ccc;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.form-title {
    font-size: 32px;
    font-weight: bold;
}

.form-input {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.form-input span {
    font-size: 20px;
}

.form-input input{
    padding: 8px 12px;
    font-size: 16px;
    border-radius: 10px;
    border: 1px solid #ccc;
}

.is-show-pass{
    display: flex;
    justify-content: end;
}

.is-show-pass:hover {
    cursor: pointer;
}


.form-note span{
    font-size: 14px;
    font-weight: 200;
}

.form-note span a{
    color: deepskyblue;
    text-decoration: underline;
}

.form-note span a:hover{
    cursor: pointer;
}

.btn-login button {
    width: 100%;
    height: 48px;
    font-size: 16px;
    border: none;
    background-color: black;
    color: white;
    border-radius: 62px;
}
